import React from "react"
import StepCounter from "./steps";
import StepOne from "./first-step";
import StepTwo from "./second-step";
import StepThree from "./third-step";
import SubmittedForm from "./after-submit";

export default class App extends React.Component {
  
  state = {
    stepNumber: 1,
    firstName: "",
    lastName: "",
    widgetName: "",
    widgetColour : "",
    email: "",
    phone:"",
    success: false,
    submit: false
  };

  reset = () =>{
    this.setState({
      stepNumber: 1,
      firstName: "",
      lastName: "",
      widgetName: "",
      widgetColour : "",
      email: "",
      phone:"",
      success: false,
      submit: false
    })
  };


  next = () => {
    this.setState(prevState => {
      return {
        stepNumber: prevState.stepNumber + 1
    }})
  };

  back = () => {
    this.setState(prevState => {
      return {stepNumber: prevState.stepNumber - 1
    }})
  };

  onChange = input => e => {
    this.setState({
      [input]: e.target.value
    })
  };
  
  onSubmit = () =>{
    this.setState({
      success: true
    }, () => this.next())
  };

  renderStepForm(stepNumber, values) {
    switch (stepNumber) {
      case 1:
        return (
          <StepOne 
          values = {values} 
          onChange = {this.onChange}
          next = {this.next}
          />
        )
      case 2:
        return (
          <StepTwo 
          values = {values} 
          onChange = {this.onChange}
          next = {this.next}
          back = {this.back}
          />
        )
      case 3:
        return (
          <StepThree
          values = {values} 
          onChange = {this.onChange}
          back = {this.back}
          onSubmit = {this.onSubmit}
          />
        )
      case 4: 
        return (<SubmittedForm success = {values.success} reset = {this.reset}/>)
      default:
        return (
          <StepOne 
          values = {values} 
          onChange = {this.onChange}
          next = {this.next}
          />
        )

  } //end switch
}

render(){
    
    const { firstName, lastName, email, phone, widgetName, widgetColour, success} = this.state;
    const values = { firstName, lastName, email, phone, widgetName, widgetColour, success};
    const {stepNumber} = this.state;
    console.log (stepNumber);
    console.log (success);
    
    return (
      <div>
        <StepCounter step ={stepNumber}></StepCounter>
        <br></br>
        <hr></hr>
        <div className="formLayout">
          {this.renderStepForm(stepNumber, values)}
        </div>
        <br></br>
        <br></br>
        <hr></hr>
        {stepNumber !== 4 ? <label name="stepNumber" >Step {stepNumber} of 3 </label > : "" }
      </div>
      
    );
 
  } //end render

}






